// Dervied Input Type
export const DerivedDynamicInput = ({name, expression, inputTypes, form, ...rest}) => {

	// Regular Expression to extract unique alphaneumeric values 
	var expressionParam = [ ...new Set(expression.match((/[a-z_]\w*(?!\w*\s*\()/ig)))];
	var filteredObject = [];
	var computedValue = 0;
	var computedExpression = expression;

	for(let i = 0; i < expressionParam.length; i++){
		filteredObject = inputTypes.filter(function(entry){
			return expressionParam.includes(entry.name)
		})
	}

	if(filteredObject.length == expressionParam.length){
		for (let k = 0; k < filteredObject.length; k++){
			var formValue = form.values[filteredObject[k].id];
			computedExpression = computedExpression.replace(expressionParam[k], formValue !== "" && !isNaN(formValue)? formValue: 0);  
		}
		computedValue = eval(computedExpression);
	}

	function getValue(computedValue, name){
		var element  = document.getElementById(name);
		if(element){
			var event = new Event('change', { bubbles: true });
			element.dispatchEvent(event);
			//form.setFieldValue(name, computedValue);
		}
		return computedValue;
	}

	return(
			<>
				<Input
							id={name}
							name={name}
							onChange={form.handleChange}
							value={getValue(computedValue, name)}
							readOnly={true}
							{...rest}
						/>
				</>
	)
}

export const DashedNormalInputIm = ({ name, ...rest }) => {

	return (
		<Field>
			{({ field, form }) => {
				const error = getIn(form.errors, name);
				const value = getIn(form.values, name);
				const className = !isStringEmpty(value) ? (error ? 'error' : 'success') : 'text-input';
				return (
					<>
						<Input
							name={name}
							onChange={field.onChange}
							className={`dashedForm-control bpf-inputType ${className}`}
							value={value}
							{...rest}
						/>
						{error && (
							<Label pointing="left" prompt size="small" basic color="red">
								{error}
							</Label>
						)}
					</>
				);
			}}
		</Field>
	);
};

